.input {
    position: relative;
    width: 50%;
}

.input__label {
    position: absolute;
    top: 16px;
    left: 42px;
}

.input__control {
    padding: 8px 24px;
    width: 100%;
    font-size: 16px;
    line-height: 1.17;
    border-radius: 8px;
    border: 1px solid var(--color-secondary-dark);
    background-color: transparent;

    &:focus {
        border-color: var(--color-brand-normal);
        outline: none;
    }

    &::placeholder {
        color: var(--color-secondary-dark);
        font-size: 16px;
        line-height: 1.17;
    }

    &:focus::placeholder {
        color: var(--color-primary-dark);
    }
}
